import helper from "../../../common/helper.js";
export default {
	data() {
		return {
			info: {},
			TabCur: 0,
			tabRow: ['隐患信息','隐患处理'],
			handlingOpinion: '',
			date: '请选择整改截止日期',
			today: '',
			btnArr: []
		}
	},
	methods: {
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		getButton() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Handle',
				data: {
					StepOrder: self.info.StepOrder,
					BranchId: self.info.BranchId,
					No: self.info.No
				},
			}).then(data => {
				var [error,res] = data;
				if (res.data == "暂无数据") {
				
				} else {
					var data = JSON.parse(res.data)
					
					self.btnArr = data;
					console.log(data);
				}
				
			})
		},
		commit() {
			if (!this.handlingOpinion) {
				this.showMoadel('请输入处理意见！')
			} else if (this.date == '请选择整改截止日期') {
				this.showMoadel('请选择整改截止日期')
			}else {
				uni.showLoading({
					title:'加载中',
					mask: true
				})
				let foo = {}
				foo = this.btnArr[0];
				
				this.dangerCommit(foo)
				
				
			}
		},
		
		dangerCommit(foo) {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Handle12',
				data: {
					YSCD: self.info.YSCD,
					BranchId: self.info.BranchId,
					RYXH: self.$store.state.userInfo.RYXH,
					StepOrder: self.info.StepOrder,
					next_ReceiveType: foo.ReceiveType,
					next_StepOrder: foo.StepOrder,
					No: self.info.No,
					next_BranchId: foo.BranchId,
					RecordRemarks: self.handlingOpinion,
					Endtime: self.date
				},
			}).then(data => {
				var [error,res] = data;
				uni.hideLoading();
				if (res.data == 'ok') {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel: false,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../../basic/home/home'
								})
							}
						}
					});
				} else {
					uni.showModal({
						title: '提示',
						content: '提交失败！',
						showCancel: false,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../../basic/home/home'
								})
							}
						}
					});
				}
			})
		},
	},
	onLoad(option) {
		this.info = JSON.parse(option.info)
		console.log(this.info)
	},
	mounted() {
		
		// this.date = helper.now();
		this.today = helper.now();
		
		this.getButton();
	}
}
